.steps-content {
  display: flex;
  justify-content: center;
}

span.ant-radio + * {
  font-size: 1.1875rem;
  margin-bottom: 0.5rem;
  font-family: inherit;
  font-weight: 400;
  line-height: 1.3;
  color: inherit;
}

.ant-steps {
  margin-bottom: 20px;
}

.pricing-price-detail,
.pricing-price,
header > h2 {
  text-align: left !important;
}

@media (min-width: 1400px) {
  .col-xxl {
    flex: 0 0 60%;
    max-width: 60%;
  }
}
.pricing-table-v1 {
  box-shadow: 0 10px 14px rgba(0, 0, 0, 0.2) !important;
}
